.header-text h1 {
  font-size: 40px;
  font-weight: 400;
  margin: 0 0 20px 0;
  text-align: center;
}

.article-title {
  font-size: 30px;
  line-height: 1.2;
  text-align: left;
  color: #000;
  margin-top: 12px;
  margin-bottom: 24px;
}

ol.article-ol,
ul.article-ul {
  padding: 0 20px;
}

ul.topic-ul {
  padding: 0 0;
}

ul.topic-ul > li {
  list-style-type: none;
  padding: 20px 0 0 0;
}

.related-topics-heading {
  font-size: 24px;
  font-weight: 300;
  line-height: 30px;
  color: #000000;
  margin-top: 24px;
  /* margin-left: -2px; */
}

.feedback-heading {
  margin: 0 0 10px 0;
  font-size: 15px;
}

.feedback-button {
  /* text-align: left; */
  color: #333 !important;
  font-weight: 300 !important;
  background-color: #cbd2d6 !important;
  display: inline-block !important;
  font-size: 15px !important;
  padding: 6px 8px !important;
  margin: 0px 10px 0px 0px !important;
  text-decoration: none !important;
  border: 0 #cbd2d6 solid !important;
  border-radius: 4px !important;
}
